#contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 35px;

  height: 85vh;
  width: 70%;
}

#contactDetails {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
}

#contactDetails p {
  color: var(--offWhite);
  font-size: 18px;
  text-align: center;
}
